@import '~node_modules/modern-normalize/modern-normalize.css';

h1, h2, h3, p, ul, li, input, button, img, a, body {
    margin: 0;
    padding: 0;
}

*{
    box-sizing: border-box;
}

button{
    border: none;
}

a {
    text-decoration: none;
}

ul, li{
    text-decoration: none;
    list-style: none;
}

.is-hidden {
    opacity: 0;
    pointer-events: none;
}

.container {
    margin: 0 auto;
    width: 320px;
    padding: 0px 15px;
    outline: 2px solid black;
    @media (min-width: 768px) and (max-width: 1199px) {
        width: 768px;
    }
    @media (min-width: 1200px) {
        width: 1200px;
    }
}
